<template>
  <v-layout row class="reddish-background">
    <v-flex xs1/>
    <v-flex xs10>
      <v-toolbar flat class="toolbar reddish-background">
        <svgicon icon="map" width="32" height="32" color="#fff" class="left25"></svgicon>
        <v-toolbar-title class="white--text text-uppercase app-title">Workplace Facility</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="hideSlider" :to="{ name: 'employees'}">
          <svgicon icon="search" width="24" height="24" color="#ddd"></svgicon>
        </v-btn>
        <v-btn icon>
          <svgicon icon="support" width="24" height="28" color="#ddd"></svgicon>
        </v-btn>
    <v-menu offset-y origin="center center" :nudge-bottom="10" transition="scale-transition">

      <v-btn icon flat slot="activator" @click="notifications.map(x => x.isActive = false)">
        <v-badge color="#2b363b" overlap>
          <span slot="badge" v-if="notifications.filter(x => x.isActive).length > 0">
             {{ notifications.filter(x => x.isActive).length  }}
          </span>
           <svgicon icon="notification" width="25" height="32" color="#ddd"></svgicon>
        </v-badge>
      </v-btn>

      <v-card class="elevation-0">
        <v-toolbar card dense color="transparent">
          <v-toolbar-title><h5>You have {{ notifications.length }} new notification(s)</h5></v-toolbar-title>
        </v-toolbar>
        <v-card-text class="pa-0">
          <v-list two-line class="pa-0">
          <template v-for="(item, index) in notifications">
            <v-divider :key="index" />
            <v-list-tile avatar :key="item.title" @click="item.onClick">
            <v-list-tile-avatar :color="item.color">
              <v-icon dark>{{item.icon}}</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-sub-title v-html="item.title"></v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action class="caption">
              {{item.actionAt}}
            </v-list-tile-action>
            </v-list-tile>
          </template>
          </v-list>
          <v-divider></v-divider>
          <v-btn block flat v-if="false">See all notifications</v-btn>
          <v-divider></v-divider>
        </v-card-text>
      </v-card>
    </v-menu>
        <userInfo></userInfo>
      </v-toolbar>
    </v-flex>
    <v-flex xs1/>
  </v-layout>
</template>

<script>
import '@/compiled-icons/map'
import '@/compiled-icons/notification'
import '@/compiled-icons/search'
import '@/compiled-icons/support'

import UserInfo from '@/components/layout/UserInfo'
import { mapActions } from 'vuex'

export default {
    name: 'TheMainToolbar',
    components: {
        userInfo: UserInfo
    },
    methods: {
        ...mapActions('navbar', ['hideSlider'])
    },
    data() {
      return {
              notifications:
      [
        {
          title: 'New hazard from Adam Joe : Brocken glass',
          color: 'light-blue',
          icon: 'email',
          actionAt: '12 min ago',
          isActive: true,
          onClick: () => {
            const vm = this;

            vm.$router.push({ name: 'notifications' });
          }
        },
        {
          title: 'Expired hazard from Mark Richards: brocken gl.',
          color: 'red',
          icon: 'calendar_today',
          actionAt: '15 days ago',
          isActive: true,
          onClick: () => {
            const vm = this;

            vm.$router.push({ name: 'calendar' });
          }
        },
        {
          title: 'New hazard from Simon Sun: room issues',
          color: 'green',
          icon: 'email',
          isActive: true,
          actionAt: '2 hour ago',
          onClick: () => {
            const vm = this;

            vm.$router.push({ name: 'notifications' });
          }
        }
      ]
      }
    }
}
</script>

<style lang="scss">
@import '@/styles/global.scss';

.app-title {
    font-family: Roboto;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffffff !important;
}

.reddish-background {
    background-color: $reddish !important;
}

.toolbar {
    height: $toolbar-height;
}

.left25 {
    margin-left: -20px;
}

.badgeText {
    position: absolute;
    font-family: Roboto;
    font-size: xx-small;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffffff;
}

#badge {
    top: -1px;
    position: absolute;
    width: 17px;
    height: 17px;
}
</style>
