<template>
    <v-content>
        <TheMainToolbar></TheMainToolbar>
        <TheAdminToolbar v-if="showAdminToolbar"></TheAdminToolbar>
    </v-content>
</template>

<script>
import TheAdminToolbar from '@/components/layout/TheAdminToolbar'
import TheMainToolbar from '@/components/layout/TheMainToolbar'
import { mapState } from 'vuex'

export default {
    name: 'toolbar',
    data: () => ({}),
    computed: {
        ...mapState('toolbar', ['showAdminToolbar'])
    },
    components: {
        TheAdminToolbar: TheAdminToolbar,
        TheMainToolbar: TheMainToolbar
    }
}
</script>